.is-hidden {
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
  }
  
  .backdrop { 
    position: fixed;
    top: 0;
    left: 0;
    z-index: 100;
    width: 100%;
    height: 100%;
    background-color: rgba(46, 47, 66, 0.4);
    transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1), visibility 250ms cubic-bezier(0.4, 0, 0.2, 1);
  }

  .modal-close-btn {
    position: relative;
    left: 66%;
    top: 200%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: transparent;
    border: none;
    
  }
  
  .modal-close-png {
    width: 24px;
    height: 24px;
    stroke: #111;
  }
  
  .modal {
    position: absolute;
    width: 335px;
    transform: translate(-50%, -50%); 
   /* max-height: 100vh; */
    top: 50%;
    left: 50%;
    overflow-y: auto;
    margin: 0 auto;
    border-radius: 18px;
    border: 2px solid #111;
    background: var(--light-theme-bcg-header);
    padding: 40px 22px;
    overflow-x: hidden;
  }

  .modal-book-img {
    width: 287px;
    height: 408px;
    margin-bottom: 14px;
  }
  .modal-book-name {
    color: var(--light-theme-primary-text-color);
    font-size: 16px;
    font-weight: 700;
    line-height: 1.12;
    letter-spacing: -0.64px;
    margin-bottom: 4px;
  }
  .modal-book-autor {
    color: var(--light-theme-second-text-color);
    font-size: 12px;
    font-style: italic;
    line-height: 1.16;
    letter-spacing: -0.48px;
    margin-bottom: 18px;
  }
  .modal-book-info {
    color: var(--light-theme-primary-text-color);
    font-size: 14px;
    line-height: 1.28;
    letter-spacing: -0.56px;
    margin-bottom: 14px;
  }
  .modal-box-link {
    display: flex;
    align-items: center;
    gap: 20px;
    margin-bottom: 40px;
  }

  .modal-linc-svg{
    filter: grayscale(100%);
  }
  /* .modal-link {
    display: flex;
    gap: 20px;
  } */
  
  .modal-add-btn {
    width: 211px;
    border-radius: 40px;
    border: 2px solid #4f2ee8;
    padding: 14px 0px;
    color: var(--light-theme-primary-text-color);
    background-color: var(--light-theme-bcg-heade);
    font-size: 14px;
    font-weight: 700;
    line-height: 1.28;
    letter-spacing: -0.14px;
    text-transform: uppercase;
    display: block;
    margin: 0 auto;
  }
  
  .modal-remove-btn {
    width: 279px;
    border-radius: 40px;
    border: 2px solid #4f2ee8;
    padding: 14px 0px;
    color: var(--light-theme-primary-text-color);
    background-color: var(--light-theme-bcg-heade);
    font-size: 14px;
    font-weight: 700;
    line-height: 1.28;
    letter-spacing: -0.14px;
    text-transform: uppercase;
    display: block;
    margin: 0 auto;
  }
  
  .modal-remove-text {
    /* display: none; */
    color: rgba(17, 17, 17, 0.5);
    text-align: center;
    font-size: 10px;
    line-height: 1.2;
    letter-spacing: -0.4px;
    margin-top: 8px;
  }


  @media screen and (min-width: 768px) {
    .modal {
        width: 579px;
        padding: 40px 40px;
        /* position: absolute; */
        /* top: 50%;
        left: 50%;
        transform: translate(-50%, -50%); */
      }
      /* .modal-close-btn {
        top: 24px;
        right: 24px;
      } */
     
      .modal-book-img {
        width: 192px;
        height: 281px;
        margin-bottom: 40px;
        margin-right: 24px;
      }

      .modal-book-name {
        width: 279px;
        font-size: 24px;
        line-height: 1.17;
        letter-spacing: -0.96px;
        margin-bottom: 8px;
      }

      .modal-book-autor {
        font-size: 14px;
        line-height: 1.29;
        letter-spacing: -0.56px;
